import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import { motion, AnimatePresence, Variants } from 'framer-motion';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import './App.css';

function App() {
  const [message, setMessage] = useState('');
  const [history, setHistory] = useState(JSON.parse(localStorage.getItem('chatHistory')) || []);
  const [isOpen, setIsOpen] = useState(false);
  const [isDark, setIsDark] = useState(localStorage.getItem('theme') === 'dark');
  const [isTyping, setIsTyping] = useState(false);
  const [typedResponse, setTypedResponse] = useState('');

  const chatRef = useRef(null);
  const inputRef = useRef(null);

  const toggleChat = () => setIsOpen(!isOpen);

  const sendMessage = async () => {
    if (!message) return;
    const newHistory = [...history, { role: 'user', content: message }];
    setHistory(newHistory);
    localStorage.setItem('chatHistory', JSON.stringify(newHistory));
    setMessage('');
    setIsTyping(true);
    setTypedResponse('');

    try {
      // Invia history + message al backend per contesto
      const res = await axios.post('http://localhost:8000/chat', { message, history: newHistory });
      // Simula typing effect
      const fullResponse = res.data.response;
      let i = 0;
      const typingInterval = setInterval(() => {
        if (i < fullResponse.length) {
          setTypedResponse((prev) => prev + fullResponse[i]);
          i++;
        } else {
          clearInterval(typingInterval);
          const updatedHistory = [...newHistory, { role: 'bot', content: fullResponse }];
          setHistory(updatedHistory);
          localStorage.setItem('chatHistory', JSON.stringify(updatedHistory));
          setIsTyping(false);
          setTypedResponse('');
        }
      }, 30); // Velocità typing: 30ms per carattere
    } catch (error) {
      const updatedHistory = [...newHistory, { role: 'bot', content: 'Errore: ' + error.message }];
      setHistory(updatedHistory);
      localStorage.setItem('chatHistory', JSON.stringify(updatedHistory));
      setIsTyping(false);
    }
  };

  const clearHistory = () => {
    setHistory([]);
    localStorage.removeItem('chatHistory');
  };

  useEffect(() => {
    if (chatRef.current) chatRef.current.scrollTop = chatRef.current.scrollHeight;
  }, [history, typedResponse]);

  useEffect(() => {
    if (isOpen && inputRef.current) inputRef.current.focus();
  }, [isOpen]);

  useEffect(() => {
    const handleStorageChange = (e) => {
      if (e.key === 'theme') {
        setIsDark(e.newValue === 'dark');
      }
    };
    window.addEventListener('storage', handleStorageChange);

    const observer = new MutationObserver(() => {
      setIsDark(document.body.classList.contains('dark-mode'));
    });
    observer.observe(document.body, { attributes: true, attributeFilter: ['class'] });

    return () => {
      window.removeEventListener('storage', handleStorageChange);
      observer.disconnect();
    };
  }, []);

  const popupVariants: Variants = {
    hidden: { scale: 0, opacity: 0, transformOrigin: 'bottom right' },
    visible: { scale: 1, opacity: 1, transformOrigin: 'bottom right' },
  };

  return (
    <div className={`app ${isDark ? 'dark' : ''}`}>
      <motion.div
        className="chatbot-icon"
        onClick={toggleChat}
        whileHover={{ scale: 1.1, boxShadow: '0 0 10px rgba(69, 182, 254, 0.5)' }}
        animate={{ scale: 1 }}
        transition={{ duration: 0.3 }}
      >
        <img src="https://res.cloudinary.com/dqhbriryo/image/upload/v1753114982/icona_chatbot_miosito_kkpny9.webp" alt="Icona" />
      </motion.div>
      <AnimatePresence>
        {isOpen && (
          <motion.div
            className="chat-popup"
            variants={popupVariants}
            initial="hidden"
            animate="visible"
            exit="hidden"
            transition={{ duration: 0.5, ease: 'easeInOut' }}
            role="dialog"
            aria-modal="true"
            aria-labelledby="chat-title"
          >
            <button className="chat-close" onClick={toggleChat}>×</button>
            <h3 className="title" id="chat-title">Chatbot AI per <span className="gradient-text">Info Servizi</span></h3>
            <div className="chat-history" ref={chatRef}>
              {history.map((msg, idx) => (
                <motion.div
                  key={idx}
                  className={`msg-bubble ${msg.role}`}
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.3 }}
                  role="log"
                  aria-live="polite"
                >
                  <p>
                    <ReactMarkdown remarkPlugins={[remarkGfm]}>{msg.content}</ReactMarkdown>
                  </p>
                </motion.div>
              ))}
              {isTyping && typedResponse && (
                <motion.div
                  className="msg-bubble bot"
                  initial={{ opacity: 0, y: 10 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.3 }}
                >
                  <p className="typing-text">
                    <ReactMarkdown remarkPlugins={[remarkGfm]}>{typedResponse}</ReactMarkdown>
                  </p>
                </motion.div>
              )}
              {isTyping && !typedResponse && (
                <motion.div
                  className="msg-bubble bot typing"
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  transition={{ repeat: Infinity, duration: 1, repeatType: 'reverse' }}
                >
                  <span className="dot">.</span><span className="dot">.</span><span className="dot">.</span>
                </motion.div>
              )}
            </div>
            <div className="input-area">
              <input
                ref={inputRef}
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                placeholder="Come posso aiutarti?"
                onKeyPress={(e) => e.key === 'Enter' && sendMessage()}
              />
              <button onClick={sendMessage} className="icon-btn send-btn">
                <i className="fas fa-paper-plane"></i>
              </button>
              <button onClick={clearHistory} className="icon-btn clear-btn">
                <i className="fas fa-trash"></i>
              </button>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}

export default App;